/*
*	BASE
*/
a:hover span { border-bottom: 1px dotted; }

.row .dashedtext .link { border-bottom: 1px dotted #a1a1a1; }
.row .dashedtext .icon { border: none; }

.table tr.even, .table tr.alt, .table tr:nth-of-type(even) { background: #f7f7f7; }

ul.inline { zoom: 1; margin: 0; /*
&.three-type li{
	width:33%;
}
&.four-type li{
	width:25%;
}
*/ }
ul.inline:before, ul.inline:after { content: ""; display: block; height: 0; overflow: hidden; }
