@media only screen and (max-width: 1024px) { .menu-catalog > nav > ul > li { margin-bottom: 15px; } }
.menu-catalog > nav > ul > li:before { content: ""; position: absolute; top: -7px; left: 0; bottom: 0; width: 1px; background: #6C7A93; }
@media only screen and (max-width: 1024px) { .menu-catalog > nav > ul > li:before { top: 2px; bottom: -2px; } }
.menu-catalog > nav > ul > li:first-child:before { display: none; }
