.menu-catalog > nav > ul > li.active + li:before { display: none; }
.menu-catalog > nav > ul > li.active > a, .menu-catalog > nav > ul > li > a.dropdown:hover, .menu-catalog > nav > ul > li a.is-hovered { border: 4px solid #4d5a70; border-bottom: 0; }
.menu-catalog > nav > ul > li.active > a span, .menu-catalog > nav > ul > li > a.dropdown:hover span, .menu-catalog > nav > ul > li a.is-hovered span { color: #000; border: 0; position: relative; z-index: 1; }
.menu-catalog > nav > ul > li.active > a::before, .menu-catalog > nav > ul > li > a.dropdown:hover::before, .menu-catalog > nav > ul > li a.is-hovered::before { content: ''; position: absolute; height: 9px; bottom: -4px; right: 4px; left: 4px; background: #fff; z-index: 1000; }
@media only screen and (max-width: 1024px) { .menu-catalog > nav > ul > li.active > a::before, .menu-catalog > nav > ul > li > a.dropdown:hover::before, .menu-catalog > nav > ul > li a.is-hovered::before { height: 20px; bottom: -20px; } }
.menu-catalog > nav > ul > li.active > a:after, .menu-catalog > nav > ul > li > a.dropdown:hover:after, .menu-catalog > nav > ul > li a.is-hovered:after { content: ""; position: absolute; left: 4px; right: 4px; bottom: 0; top: 4px; background: #fff; border-radius: 3px 3px 0 0; }
@media only screen and (max-width: 1024px) { .menu-catalog > nav > ul > li.active > a:after, .menu-catalog > nav > ul > li > a.dropdown:hover:after, .menu-catalog > nav > ul > li a.is-hovered:after { top: -2px; } }
.menu-catalog > nav > ul > li > a { font-size: 1rem; color: #fff; padding: 0.875rem 1.0625rem 1.25rem 1.0625rem; display: block; border: 4px solid #5b6a84; border-bottom: 0; -webkit-border-radius: 5px; border-radius: 5px; }
.menu-catalog > nav > ul > li > a span { color: #fff; }
.menu-catalog > nav > ul > li > a:hover span { border-bottom: 1px solid #fff; }
@media only screen and (max-width: 1024px) { .menu-catalog > nav > ul > li > a { padding: 0 15px; line-height: normal; font-size: 14px; } }
.menu-catalog > nav > .row { padding-top: 7px; padding-left: 0; }
@media only screen and (max-width: 1024px) { .menu-catalog > nav > .row { padding: 10px 0 0; } }

.f-dropdown.content { border-radius: 5px; display: none; box-shadow: 0px -4px 0px 0px rgba(0, 0, 0, 0.15); border: solid 4px rgba(0, 0, 0, 0.15); border-top: 0; }
