.bcrumbs-container .bcrumbs li a:hover span { border-bottom: 0.0625rem solid #333; }
.bcrumbs-container .bcrumbs li::after { content: ' / '; color: #333; padding: 0 0.1875rem; }
.bcrumbs-container .bcrumbs li:last-child::after { content: none; }
.bcrumbs-container .bcrumbs li:last-child a { border-bottom: none; }
.bcrumbs-container .bcrumbs li:last-child a:hover { color: #333; }
.bcrumbs-container .bcrumbs li:last-child a:hover span { border-bottom: 0.0625rem solid #333; }

.bcrumbs li.has-drop:hover .bcrumbs__drop { display: block; }
.bcrumbs li.has-drop:hover > a:before, .bcrumbs li.has-drop:hover > a:after { display: block; }
.bcrumbs li.has-drop > a { position: relative; z-index: 2; }
