.footer-top:before, .footer-top:after { content: ""; display: block; height: 0; overflow: hidden; }
.footer-top:after { clear: both; }

.footer-bottom { position: relative; zoom: 1; background-color: #5b6a84; height: 55px; line-height: 55px; }
.footer-bottom:before, .footer-bottom:after { content: ""; display: block; height: 0; overflow: hidden; }
.footer-bottom:after { clear: both; }
@media only screen and (max-width: 767px) { .footer-bottom { height: auto; padding: 20px 0; } }

.footer-copy { position: absolute; top: 50%; left: 0; transform: translateY(-50%); }
@media only screen and (max-width: 767px) { .footer-copy { position: static; top: 0; transform: translateY(0); margin-bottom: 15px; } }
.footer-copy .copyright { margin-bottom: 0; color: #fff; font-size: 13px; font-weight: 400; }
@media only screen and (max-width: 767px) { .footer-copy .copyright_text { display: block; } }

@media only screen and (max-width: 767px) { footer .footer-copy .copyright { padding-left: 0; text-align: center; } }

.footer-link-site { position: absolute; top: 50%; right: 25px; transform: translateY(-50%); }
@media only screen and (max-width: 767px) { .footer-link-site { position: static; top: 0; transform: translateY(0); } }
.footer-link-site a { font-size: 12px; text-decoration: underline; color: #fff; }

.footer-nav { float: right; }
@media only screen and (max-width: 767px) { .footer-nav { max-width: 130px; width: 100%; display: inline-block; text-align: left; } }
.footer-nav__list { font-size: 0; list-style-type: none; padding-bottom: 0; }
.footer-nav__item { display: inline-block; vertical-align: top; margin: 0 15px; max-width: 80px; }
@media only screen and (max-width: 767px) { .footer-nav__item { display: block; max-width: none; margin: 0; margin-bottom: 10px; }
  .footer-nav__item:last-child { margin-right: 0; } }
.footer-nav__item:first-child { margin-left: 0; }
.footer-nav__item:last-child { margin-right: 0; }
.footer-nav__link { font-size: 14px; color: #0053a0; text-decoration: underline; }
@media only screen and (max-width: 767px) { .footer-nav__link { font-size: 13px; color: #000; } }


